<template>
<div class="field__setting">
    <ConfirmHeader>
        <template v-slot:title>
            <p class="field__time">Время на площадке</p>
        </template>
    </ConfirmHeader>
    <div class="field__text">{{isDaySelected?'Проставьте цены':'Укажите режим работы'}}</div>
    <working-hours @daySelected='daySelected' :saved="saved" v-show="show&&saved.length!==7"/>
    <PriceSettings v-show="!show&&!isDayOff" :selectedDays="selectedDaysF" :workingHours="workingHours" @readyToChange="readyToChange"/>
    <div class="wrapper">
        <ChangedCard style="margin-bottom: 16px" v-for="e, index in changedInfo" :key="index" v-show="show" :info="e" :start_time="e.start" :end_time="e.end" @clear="clear"/>
    </div>
    <div class="save__btn" :class="{'available': saved.length===7, 'notDisplayed':!show}" @click="commitChanges()">
        Сохранить
    </div>
</div>
</template>
<script>
import ConfirmHeader from '../components/ConfirmHeader.vue'
import WorkingHours from '../components/WorkingHours.vue'
import PriceSettings from '../components/PriceSettings'
import ChangedCard from '../components/ChangedCard'
import { mapState } from 'vuex'
import axios from 'axios'
export default {
    data() {
        return{
            isDaySelected: false,
            sendData: {},
            selectedDaysF: [],
            selectedDays: [],
            isDayOff: false,
            workingHours: [],
            changedInfo: [],
            saved: [],
            show: true,
            showInfo: false,
            daysToChange: [],
            facility: JSON.parse(localStorage.getItem('facility'))
        }
    },
    components: {ConfirmHeader, WorkingHours, PriceSettings, ChangedCard},
    methods:{
        clear(info){
            this.selectedDays = this.selectedDays.filter(e=>!info.selectedDays.includes(e));
            this.saved = this.dayFormatReversed(this.dayFormat(this.saved).filter(e=>!info.selectedDays.includes(e)));
            
            this.changedInfo = this.changedInfo.filter(e=>JSON.stringify(e)!==JSON.stringify(info));
            this.showInfo = false;
            this.show = true;
        },
        dayFormat(days){
            return days.map(e=>{
                switch(e){
                    case '1':
                        return 'Пн';
                    case '2':
                        return 'Вт';
                    case '3':
                        return 'Ср';
                    case '4':
                        return 'Чт';
                    case '5':
                        return 'Пт';
                    case '6':
                        return 'Сб';
                    case '0':
                        return 'Вс';
                }
            });
        },
        dayFormatReversed(days){
            return days.map(e=>{
                switch(e){
                    case 'Пн':
                        return '1';
                    case 'Вт':
                        return '2';
                    case 'Ср':
                        return '3';
                    case 'Чт':
                        return '4';
                    case 'Пт':
                        return '5';
                    case 'Сб':
                        return '6';
                    case 'Вс':
                        return '0';
                }
            });
        },
        daySelected(selectedDays, isDayOff, workingHours){
            this.show = false;
            this.selectedDays = selectedDays;
            this.selectedDaysF = this.dayFormat(selectedDays)
            this.isDayOff = isDayOff;
            this.workingHours = workingHours;

            if(this.isDayOff){
                this.showInfo = true;
                this.show = true;
                this.saved.push(...selectedDays);
                this.changedInfo.push({'dayOff': this.isDayOff, 'selectedDays': this.selectedDays})
                this.selectedDays.forEach(e=>{
                    this.sendData[e] = {'dayoff': true}
                })
            }
        },
        readyToChange(info, start, end, sendData){
            this.changedInfo.push({'info': info, 'dayOff': this.isDayOff, 'selectedDays': this.selectedDays, start: start, end: end});
            this.selectedDays.forEach(e=>{
                this.sendData[[e]] = {
                    'start_time': start,
                    'end_time':end,
                    'prices': {...sendData},
                    'dayoff': false
                }
            });
            this.saved.push(...this.selectedDays)
            this.selectedDays = [];
            this.selectedDaysF = [];
            this.show = true;
            this.showInfo = true;
        },
         async commitChanges(){
             this.facility.forEach(e=>{
                 if(e.id===this.playfield.id){
                     e['periods_data'] = JSON.stringify(this.sendData);
                 }
             });
            await axios.post(`http://localhost/crm/user/${localStorage.getItem('id')}/playfield/${this.playfield.id}/update-data`, {periods_data: this.facility.find(e=>e.id===this.playfield.id).periods_data}, 
            {
                headers: {'Authorization': `Bearer ${localStorage.getItem('access_token')}`}
            }).then(e=>console.log(e)).catch(e=>console.log(e));
            localStorage.setItem('facility', JSON.stringify(this.facility));
    
        }
    },
    computed: {
        ...mapState('playfield', ['playfield'])
    }
}
</script>
<style lang="scss" scoped>
*{
    font-family: 'Roboto', sans-serif;
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    line-height: 20px;
    color: #000;
}
.field__setting{
    min-height: 100vh;
    background: #E5E5E5;
    position: relative;
}
.field__text{
    font-family: 'Roboto', sans-serif;
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    line-height: 20px;
    padding: 16px;
    margin: 16px 0px;
    background: #fff;
}
.save__btn{
    margin-left: 16px;
    margin-bottom: 24px;
    margin-top: 16px;
    width: 84px;
    background: #C4C4C4;
    padding: 5px 7px;
    pointer-events: none;
}
.notDisplayed{
    display: none;
}
.available{
    background: #000;
    color: #fff;
    pointer-events: initial;
}
</style> 