<template>
    <div class="playfield__workingHours" @click.stop="goToWorkingHours()">
        <div class="flex">
            <div class="flex__key">Режим работы</div>
            <i class="fas fa-chevron-right"></i>
        </div>
        <div class="value">
            <div v-for="key, index in keys" :key="index" class="flex">
                <div class="days" style="display:flex">
                    <p v-for="day, i in wh[key]" :key="i">{{getDay(day)}}</p>
                </div>
                <p>{{key?key:Выходной}}</p>
            </div>
        </div>
    </div>    
</template>
<script>
import dayjs from 'dayjs';
import {mapState} from 'vuex';
export default {
    data(){
        return{
            wh: [],
            keys: []
        } 
    },
    mounted(){
        this.getWorkingHours();
    },
    methods: {
        goToWorkingHours(){
            this.$router.push({name: 'playfieldSettings'})
        },
        getWorkingHours(){
            Object.keys(JSON.parse(this.playfield.periods_data)).map(key=>{
                let s = JSON.parse(this.playfield.periods_data)[key].start_time;
                let e = JSON.parse(this.playfield.periods_data)[key].end_time;
                let kk = s&&e?`${s}-${e}`:'Выходной';
                if(this.keys.includes(kk)){
                    this.wh[kk].push(key);
                }
                else{
                    this.wh[kk] = [key];
                    this.keys.push(kk);
                }
            });
        },
        getDay(day){ 
            return dayjs().day(parseInt(day)).locale('ru').format('dd')+',';
        }
    },
    computed: {
        ...mapState('playfield', ['playfield'])
    }
}
</script>
<style lang="scss" scoped>
:not(i){
    font-family: 'Roboto', sans-serif;
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    line-height: 20px;
}
.playfield{
    &__workingHours{
            padding: 16px;
            background: #fff;
            box-shadow: inset 0px -1px 0px rgba(0, 0, 0, 0.1);
            .value{ 
                color: rgba(0, 0, 0, 0.6);
            }
            .flex{
                display: flex;
                justify-content: space-between;
                align-items: center;
                margin-bottom: 6px;
                &__center{
                    display: flex;
                    justify-content: center;
                    align-items: center;
                }
                &__key{
                    font-size: 16px;
                }
                i{
                    color: rgba(0, 0, 0, 0.6);
                }
            }
        }
}
</style>