<template>
    <div class="myFacilities">
        <Spinner :start="start"/>
        <MenuHeader :backButton = false>
            <template v-slot:title>
                <p class="my-facilities">Мои сооружения</p>
            </template>
        </MenuHeader>
        <FacilityCard v-for="f in facilities" :key="f.id" :facility="f"/>
        <BottomMenu />
    </div>
</template>
<script>
import MenuHeader from '../components/MenuHeader.vue'
import BottomMenu from '../components/BottomMenu.vue'
import FacilityCard from '../components/FacilityCard.vue'
import axios from 'axios'
import Spinner from '../components/Spinner.vue'
import {mapState} from 'vuex'
export default {
    components: {MenuHeader, BottomMenu, FacilityCard, Spinner},
    data(){
        return{
            start: true, 
            facilities: []
        }
    },
    async created(){
        this.facilities = await axios.get(`http://localhost/crm/user/${localStorage.getItem('id')}/facilities`, {
            headers: {'Authorization': `Bearer ${localStorage.getItem('access_token')}`}
        }).then(res=>{
            this.$store.dispatch('facility/setFacility', res.data.user.facilities[0])
            this.start = false;
            return res.data.user.facilities
        }).catch(e=>console.log(e));
    },
    computed: {...mapState('admin', ['user'])}
}
</script>
<style lang="scss" scoped>
:not(i){
    font-family: 'Roboto', sans-serif;
    font-weight: normal;
    font-style: normal;
    font-size: 14px;
    line-height: 20px;
}
.myFacilities{
    background: #E5E5E5;
    min-height: 100vh;
}
p.my-facilities{
    font-weight: 500;
    font-size: 16px;
    line-height: 24px;
    text-align: center;
    letter-spacing: 0.15px;
    color: rgba(0, 0, 0, 0.87);
}
</style>