<template>
    <div class="facility">
        <MenuHeader :backButton="false">
            <template v-slot:title>
                <p class="facility__title">
                    {{facility.fc_name}}
                </p>
            </template>
        </MenuHeader>
        <div class="content">
            <div class="content__inner">
                <div class="add__photo">
                    <div class="backdrop" v-show="photoAction" @click.stop="photoAction=false"></div>
                    <div class="photo__icon" @click="photoAction = true">
                        <input type="file" name="file" ref="facility__photo" @change="setPhoto">
                        <i class="fas fa-camera" v-if="hasNotPhoto"></i>
                        <img v-else :src="`http://localhost/${facility.avatar}`">
                    </div>
                </div>
                <FacilityName />
                <FacilityAddress/>
                <FacilityContact />
                <FacilityDescription/>
                <FacilityInfrastructure/>
                <FacilityPayment/>
                <p class="playfields">Площадки</p>
                <playfield-card @goToPlayfield="goToPlayfield" v-for="p in facility.playfields" :key="p.id" :playfield="p"/>
            </div>
        </div>
        <PhotoModal v-show="photoAction" @changePhoto="changePhoto" @deletePhoto="deletePhoto" :needed="true"/>
        <bottom-menu/>
    </div>
</template>
<script>
import BottomMenu from '../components/BottomMenu.vue'
import MenuHeader from '../components/MenuHeader.vue'
import PlayfieldCard from '../components/PlayfieldCard.vue'
import FacilityName from '../components/FacilityName.vue'
import FacilityAddress from '../components/FacilityAddress.vue'
import {mapState} from 'vuex';
import FacilityDescription from '../components/FacilityDescription.vue'
import FacilityInfrastructure from '../components/FacilityInfrastructure.vue'
import FacilityPayment from '../components/FacilityPayment.vue'
import FacilityContact from '../components/FacilityContact.vue'
import PhotoModal from '../components/PhotoModal.vue'
import axios from 'axios'
export default {
    data(){
        return{
            photo: '',
            photoAction:false,
        }
    }, 
    components: {MenuHeader, PlayfieldCard, BottomMenu, FacilityName, FacilityAddress, FacilityDescription, FacilityInfrastructure, FacilityPayment, FacilityContact, PhotoModal},
    methods: {
        goToPlayfield(p_id){
           this.$router.push({name: 'playfield', params: {id: this.facility.id, p_id: p_id}})
        },
        changePhoto(){
            this.$refs['facility__photo'].click();
        },
        async setPhoto(){
            this.photoAction = false;
            this.photo = this.$refs['facility__photo'].files[0];
            let formData = new FormData();
            formData.append('file', this.photo);
            await axios.post( `http://localhost/crm/user/${localStorage.getItem('id')}/facility/${this.facility.id}/load-avatar`,
              formData,
              {
                headers: {
                    'Content-Type': 'multipart/form-data',
                    'Authorization': `Bearer ${localStorage.getItem('access_token')}`
                }
              }
            ).then(()=>{
                this.$store.dispatch('facility/resetFacility');
            })
            .catch(function(){
              console.log('FAILURE!!');
            });
        },
        async deletePhoto(){
            axios.post(`http://localhost/crm/user/${localStorage.getItem('id')}/facility/${this.facility.id}/remove-avatar`).then(()=>this.$store.dispatch('facility/resetFacility')).catch(e=>console.log(e));
            this.photoAction = false;
        }
    }, computed: {...mapState('facility', ['facility']),
    hasNotPhoto(){
        return this.facility.avatar==='default_avatar.png'
    }}
}
</script>
<style lang="scss" scoped>
:not(i){
    font-family: 'Roboto', sans-serif;
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    line-height: 20px;
}
    .content{
        display: flex;
        justify-content: center;
        align-items: center;
        margin-bottom: 49px;
        .content__inner{
            width: 375px;
        }
    }
.facility{
    min-height: 100vh;
    background: #E5E5E5;
    &__title{
        font-weight: 500;
        font-size: 16px;
        line-height: 24px;
        text-align: center;
        letter-spacing: 0.15px;
        color: rgba(0, 0, 0, 0.87);
    }
    &__name, &__address, &__description, &__infrastructure, &__payment{
        padding: 16px;
        background: #fff;
        box-shadow: inset 0px -1px 0px rgba(0, 0, 0, 0.1);
        .value{ 
            color: rgba(0, 0, 0, 0.6);
        }
    }
}
.flex{
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 6px;
    &__key{
        font-size: 16px;
    }
    i{
        color: rgba(0, 0, 0, 0.6);
    }
}
.add__photo{
    display: flex;
    justify-content: center;
    align-items: center;
    height: 92px;
    .photo__icon{
        background: rgba(0, 0, 0, 0.12);
        border-radius: 2px;
        display: flex;
        justify-content: center;
        align-items: center;
        width: 60px;
        height: 60px;
        position: relative;
        input{
            position: absolute;
            width: 0;
            height: 0;
            opacity: 0;
        }
        img{
            position: absolute;
            max-width: 100%;
            max-height: 100%;
        }
    }
}
p.playfields{
    color: rgba(0, 0, 0, 0.6);
    margin: 24px 0px 8px 16px;
    font-size: 12px;
    font-weight: 500;
    letter-spacing: 0.05em;
    text-transform: uppercase;

}
.backdrop {
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 10;
}
</style>