<template>
    <div class="longTermSearch">
            <order-header>
                <template v-slot:orderHeader>
                    <p class= "new__order">Результаты поиска</p>
                    <div class="close__order" @click="goBack()">
                        <i class="fa fa-times-circle" aria-hidden="true"></i>
                    </div>
                </template>
            </order-header>
        <div class="longTermSearch__inner">
            <div class="little__info">
                <p class="first__line">Площадка №{{booking.field_id}}, {{getDayOfWeek(order.start_date)}}</p>
                <p class="second__line">С {{getDateFormat(order.start_date)}} по {{getDateFormat(order.end_date)}}</p>
                <p class="third__ling">Доступно {{getAvailable}} из {{getAllBookingsCount}}</p>
            </div>
            <div class="results">
                <booking-availability v-for="date, index in bookings" @handleSelect="handleSelect" @addValue="addValue" :date="date" :isFree="isFree(date)" :key="index"/>
            </div>
        </div>
        <div class="information">
            <p class="selected">Выбрано {{count}} броней</p>
            <p class="all__price">Общая сумма: {{getAllCount}} тг</p>
            <div class="continue" @click="createOrder()">Продолжить</div>
        </div>
    </div>
</template>
<script>
import OrderHeader from '../components/OrderHeader.vue'
import BookingAvailability from '../components/BookingAvailability.vue'
import {mapState, mapGetters} from 'vuex';
import * as dayjs from 'dayjs'
import 'dayjs/locale/ru'
export default {
    data(){
        return{
            bookings: [],
            timelines: ["05:00", "05:30", "06:00", "06:30", "07:00", "07:30", "08:00", "08:30", "09:00", "09:30", "10:00", "10:30", "11:00", "11:30", "12:00", "12:30", "13:00", "13:30", "14:00", "14:30", "15:00", "15:30", "16:00", "16:30", "17:00", "17:30", "18:00", "18:30", "19:00", "19:30", "20:00", "20:30", "21:00", "21:30", "22:00", "22:30", "23:00", "23:30", "00:00", "00:30", "01:00", "01:30", "02:00", "02:30", "03:00", "03:30", "04:00", "04:30"],
            orders: JSON.parse(localStorage.getItem('orderInfo')),
            selectedDates:[],
            count: 0
        }
    },
    created(){
        this.getAllBookings();

    },
    mounted(){
        this.count = this.getSelectedBookingsCount();
    },
    components: {OrderHeader, BookingAvailability},
    computed: {
        ...mapState('order', ['order']),
        ...mapGetters('booking', ['getBooking']),
        ...mapState('booking', ['booking']),
        getAllBookingsCount(){
            let s = this.order.start_date;
            let e = this.order.end_date;
            return e.diff(s, 'week')+1;
        },
        getAvailable(){
            return this.selectedDates.filter(e=>e.isSelected).length;
        },
        getAllCount(){
            return this.count*this.booking.price;
        }
    },
    methods:{
        addValue(obj){
            this.selectedDates.push(obj);
        },
        handleSelect(obj){
            this.selectedDates.find((e, idx)=>{
                if(this.checkDates(e.date, obj.date)){
                    this.selectedDates[idx] = obj;
                    return true;
                }
            });
            this.count = this.getSelectedBookingsCount();
        },
        getSelectedBookingsCount(){
            return this.selectedDates.filter(e=>e.isSelected).length;
        },
        getDayOfWeek(day){
            return dayjs(day).locale('ru').format('dddd')
        },
        getDateFormat(day){
            return dayjs(day).locale('ru').format('DD MMMM')
        },
        getAllBookings(){
            let s = this.order.start_date;
            const weeks = this.getAllBookingsCount;
            for(let i =  0;i<weeks;i++){
                this.bookings.push(s);
                s = s.add(7, 'day');
            }
        },
        checkDates(date1, date2){
            return dayjs(date1).locale('ru').format('DD-MMMM-YYYY')===dayjs(date2).locale('ru').format('DD-MMMM-YYYY')
        },
        checkFields(f1, f2){
            return f1===f2;
        },
        isFree(date){
            for(const key in this.orders){
                const bkngs = this.orders[key].bookings;
                for(const b in bkngs){
                    if(this.checkDates(bkngs[b].date, date)&&this.isOverlapping(bkngs[b].start_time, bkngs[b].end_time)&&this.checkFields(this.field_id, bkngs[b].field_id)){
                        return false;
                    }
                }
            }
            return true;
        },
        isOverlapping(st, et){
            const changed_st_idx = this.timelines.indexOf(this.booking.start_time);
            const changed_et_idx = this.timelines.indexOf(this.booking.end_time);
            const st_idx = this.timelines.indexOf(st);
            const et_idx = this.timelines.indexOf(et)
            return !(changed_st_idx>=et_idx||changed_et_idx<=st_idx);
        },
        createOrder(){
            let fake_id = 1;
            const bookings = [];
            this.selectedDates.filter(e=>e.isSelected).forEach(e=>{
                this.$store.dispatch('booking/setBookingDate', e.date);
                this.$store.dispatch('booking/setBookingId', fake_id);
                bookings.push(this.getBooking);
                fake_id+=1;
            });
            this.$store.dispatch('order/replaceBooking', bookings)
            this.$router.push({path: '/order'})
        }   
    }
}
</script>
<style lang="scss" scoped>
:not(i){
    font-family: 'Roboto', sans-serif;
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    line-height: 16px;
}
.longTermSearch{
     min-height: 100vh;
     overflow: auto;
}
.longTermSearch__inner{
    padding: 12px 16px;
    margin-bottom: 131px;
    .little__info{
        margin-bottom: 16px;
    }
}
.information{
    position: fixed;
    bottom: 0;
    left: 0;
    right: 0;
    height: 131px;
    padding: 24px 16px;
    z-index: 2;
    background: #fff;
    box-shadow: 0px -4px 8px rgba(0, 0, 0, 0.04), 0px 0px 2px rgba(0, 0, 0, 0.06), 0px 0px 1px rgba(0, 0, 0, 0.04);
    .continue{
        background: #FFFFFF;
        border: 1px solid #9D9D9D;
        display: flex;
        justify-content: center;
        align-items: center;
        height: 30px;
        margin-top: 16px;
    }
}
</style>